/* const courseId = store.getState().apiReducer.Course.items[0] */

const levelDelete = ({
  render
}) => React.createElement(Mutation, {
  mutation: DELETE_LEVEL,
  update: (cache, {
    data: {
      levelDelete
    }
  }) => {
    try {
      const gotLevels = cache.readQuery({
        query: getLevels,
        variables: "TODO: pending"
      });
      var {
        levels
      } = gotLevels.getLevels;
      cache.writeQuery({
        query: getLevels,
        data: {
          getLevels: {
            levels: remove(levels, item => item.level === levelDelete.level.level) || []
          }
        }
      });
    } catch (err) {
      console.log("err: ", err);
    }
  }
}, (mutation, result) => render({
  mutation,
  result
}));

